import $ from 'jquery';

const geoplace = (() => {
	const placeField = $('.geoplace__field');
	const placePopup = $('.popup.geoplace__place');
	const area = $('.geoplace__area');

	placeField.on('focus', ()=>{
		placePopup.addClass('.popup_show');
	})
})();

export default geoplace


